@import url("https://fonts.googleapis.com/css?family=Josefin+Sans|ZCOOL+KuaiLe");
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #21252B;
  color: #fcfdfe; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.page {
  height: 100vh;
  width: 100%; }

#frontpage {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column; }
  #frontpage .center {
    text-align: center;
    padding-bottom: 1rem; }
  #frontpage .title {
    color: #73C990;
    font-family: 'ZCOOL KuaiLe', cursive;
    font-size: 3rem;
    padding-bottom: .8rem; }

#webdev {
  background-color: #282C34;
  display: -webkit-flex;
  display: flex; }
  #webdev .hljs {
    font-size: .8rem;
    overflow: hidden; }
  #webdev .left {
    width: 28%;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    position: relative;
    z-index: 2; }
    #webdev .left::after {
      position: absolute;
      content: '';
      right: 0;
      top: 0;
      bottom: 0;
      width: 3rem;
      background: -webkit-linear-gradient(left, rgba(40, 44, 52, 0) 0%, #282c34 80%, #282c34 100%);
      background: linear-gradient(to right, rgba(40, 44, 52, 0) 0%, #282c34 80%, #282c34 100%); }
  #webdev .right {
    -webkit-flex-shrink: 1;
            flex-shrink: 1;
    background-color: #ffffff;
    width: 100%; }
